import React from "react"
import { useNavigate } from "react-router"
import { BriefReportNotGeneratedIcon } from "@rp/react/common/assets"
import { Box, Button, Typography } from "@rp/react/web/mui/core"
import { WATCHLIST_ID_ROUTE } from "@rp/react/web/router"

type BriefReportNotGeneratedProps = {
  watchlistId: string | undefined
}

const ICON_SIZE = 130

const BriefReportNotGenerated = ({ watchlistId }: BriefReportNotGeneratedProps) => {
  const navigate = useNavigate()

  return (
    <Box
      sx={{
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        height: "calc(100vh - 240px)",
        width: "750px",
        justifyContent: "center",
      }}
    >
      <BriefReportNotGeneratedIcon height={ICON_SIZE} width={ICON_SIZE} />

      <Typography fontSize="20px" fontWeight={700} pt="16px">
        Great News!
      </Typography>

      <Typography fontSize="16px" textAlign="center">
        Your brief report is on its way and will be ready shortly.
      </Typography>

      <Typography fontSize="16px" textAlign="center" mb="16px" color="text.secondary">
        Expected time: 4 minutes
      </Typography>

      <Button
        variant="contained"
        disabled={!watchlistId}
        onClick={() => watchlistId && navigate(WATCHLIST_ID_ROUTE.replace(":id", watchlistId))}
        aria-label="Back to Watchlist"
      >
        Back to Watchlist
      </Button>
    </Box>
  )
}

export { BriefReportNotGenerated }
